"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  LayoutDashboard,
  Package,
  FolderTree,
  ShoppingCart,
  Settings,
  LogOut,
  ChevronLeft,
  ChevronRight,
  Ruler,
  Palette,
  Store,
  ArrowLeft,
} from "lucide-react";
import { useAuth } from "@/lib/auth-context";
import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

const menuItems = [
  { href: "/admin", label: "Dashboard", icon: LayoutDashboard },
  { href: "/admin/produtos", label: "Produtos", icon: Package },
  { href: "/admin/categorias", label: "Categorias", icon: FolderTree },
  { href: "/admin/tamanhos", label: "Tamanhos", icon: Ruler },
  { href: "/admin/cores", label: "Cores", icon: Palette },
  { href: "/admin/pedidos", label: "Pedidos", icon: ShoppingCart },
  { href: "/admin/configuracoes", label: "Configurações", icon: Settings },
];

export function AdminSidebar() {
  const pathname = usePathname();
  const { user, logout } = useAuth();

  const [expanded, setExpanded] = useState(false);

  return (
    <motion.aside
      initial={false}
      animate={{
        width: expanded ? 260 : 80, // em px, pra animação ficar lisa
      }}
      transition={{
        type: "spring",
        stiffness: 260,
        damping: 30,
      }}
      className="
        bg-primary text-primary-foreground min-h-screen flex flex-col
        shadow-lg
      "
    >
      {/* Header */}
      <div className="relative p-6 border-b border-primary-foreground/10">
        {/* Botão expandir/recolher */}
        <button
          onClick={() => setExpanded((prev) => !prev)}
          className="
            absolute -right-3 top-6 bg-primary-foreground text-primary rounded-full p-1 
            shadow-md hover:scale-105 transition-transform
          "
          aria-label={expanded ? "Recolher menu" : "Expandir menu"}
        >
          {expanded ? <ChevronLeft size={20} /> : <ChevronRight size={20} />}
        </button>

        {/* Voltar à loja */}
        <Link
          href="/"
          title="Voltar à loja"
          aria-label="Voltar à loja"
          className={`
            flex items-center text-sm text-primary-foreground/70 hover:text-primary-foreground
            transition-colors
            ${expanded ? "gap-2 justify-start mb-4" : "justify-center"}
          `}
        >
          {expanded ? (
            <>
              <ArrowLeft size={16} />
              <span>Voltar à loja</span>
            </>
          ) : (
            <div className="flex flex-col items-center gap-1 text-[10px] leading-none">
              <Store size={18} />
              <span>Loja</span>
            </div>
          )}
        </Link>

        <AnimatePresence initial={false}>
          {expanded && (
            <motion.div
              key="admin-header-text"
              initial={{ opacity: 0, y: 8 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -4 }}
              transition={{ duration: 0.18 }}
            >
              <h1 className="text-xl font-semibold">Memoriza</h1>
              <p className="text-sm text-primary-foreground/70">
                Painel Administrativo
              </p>
              {user?.email && (
                <p className="mt-1 text-xs text-primary-foreground/60">
                  {user.email}
                </p>
              )}
            </motion.div>
          )}
        </AnimatePresence>
      </div>

      {/* Navigation */}
      <nav className="flex-1 p-4">
        <ul className="space-y-1">
          {menuItems.map((item) => {
            const Icon = item.icon;
            const isActive = pathname === item.href;

            return (
              <li key={item.href}>
                <Link
                  href={item.href}
                  className={`
                    flex items-center px-3 py-3 rounded-lg transition-colors
                    ${
                      isActive
                        ? "bg-primary-foreground/20 text-primary-foreground"
                        : "text-primary-foreground/70 hover:bg-primary-foreground/10 hover:text-primary-foreground"
                    }
                    ${expanded ? "gap-3 justify-start" : "justify-center"}
                  `}
                >
                  <Icon size={20} />
                  <AnimatePresence initial={false}>
                    {expanded && (
                      <motion.span
                        key={item.label}
                        initial={{ opacity: 0, x: -10 }}
                        animate={{ opacity: 1, x: 0 }}
                        exit={{ opacity: 0, x: -10 }}
                        transition={{ duration: 0.18 }}
                        className="whitespace-nowrap"
                      >
                        {item.label}
                      </motion.span>
                    )}
                  </AnimatePresence>
                </Link>
              </li>
            );
          })}
        </ul>
      </nav>

      {/* Footer */}
      <div className="p-4 border-t border-primary-foreground/10">
        <button
          onClick={logout}
          className={`
            flex items-center w-full px-4 py-2 text-primary-foreground/70 
            hover:text-primary-foreground hover:bg-primary-foreground/10 
            rounded-lg transition-colors
            ${!expanded ? "justify-center" : "gap-3 justify-start"}
          `}
        >
          <LogOut size={18} />
          <AnimatePresence initial={false}>
            {expanded && (
              <motion.span
                key="logout-label"
                initial={{ opacity: 0, x: -8 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: -8 }}
                transition={{ duration: 0.18 }}
              >
                Sair
              </motion.span>
            )}
          </AnimatePresence>
        </button>
      </div>
    </motion.aside>
  );
}